body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.parallelogram {
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeftTilted;
            animation: 1s ease-out 0s 1 slideInFromLeftTilted;
    position: absolute;
    z-index: 1;
    width: 90vw;
    height: 40vw;
    -webkit-transform: skew(-45deg);
            transform: skew(-45deg);
    margin-left: -54%;
    background: rgb(109, 114, 116);
}

.name{
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeftName;
            animation: 1s ease-out 0s 1 slideInFromLeftName;
    position: absolute;
    margin-top: 15%;
    margin-left: 2%;
    font-size: 5vw;
    z-index: 2
}

.title{
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeftTitle;
            animation: 1s ease-out 0s 1 slideInFromLeftTitle;
    position: absolute;
    margin-top: 15%;
    margin-left: 40%;
    font-size: 5vw;
    z-index: 1;
    color: rgb(109, 114, 116);
}

.rectangle {
    -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;
            animation: 1s ease-out 0s 1 slideInFromLeft;
    position: relative;
    width: 100vw;
    height: 40vw;
    background: rgb(0, 0, 0);
}

@-webkit-keyframes slideInFromLeft {
    0% {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }

@keyframes slideInFromLeft {
    0% {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }

  @-webkit-keyframes slideInFromLeftTitle {
    0% {
      -webkit-transform: translateX(-200%);
              transform: translateX(-200%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }

  @keyframes slideInFromLeftTitle {
    0% {
      -webkit-transform: translateX(-200%);
              transform: translateX(-200%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  @-webkit-keyframes slideInFromLeftName {
    0% {
      -webkit-transform: translateX(-60%);
              transform: translateX(-60%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  @keyframes slideInFromLeftName {
    0% {
      -webkit-transform: translateX(-60%);
              transform: translateX(-60%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  @-webkit-keyframes slideInFromLeftTilted {
    0% {
      -webkit-transform: translateX(-20%) skew(-45deg);
              transform: translateX(-20%) skew(-45deg);
    }
    100% {
      -webkit-transform: translateX(0) skew(-45deg);
              transform: translateX(0) skew(-45deg);
    }
  }
  @keyframes slideInFromLeftTilted {
    0% {
      -webkit-transform: translateX(-20%) skew(-45deg);
              transform: translateX(-20%) skew(-45deg);
    }
    100% {
      -webkit-transform: translateX(0) skew(-45deg);
              transform: translateX(0) skew(-45deg);
    }
  }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

